
.policyPreview .policyTop .mint-cell-wrapper {
  background-image: linear-gradient(180deg, #fff, #fff 50%, transparent 50%);
}
.policyPreview .policyTop img {
  width: 110px;
  height: 43px;
  margin-top: 5px;
}
.policyPreview .policyTop span {
  color: #333;
}
.policyPreview .policyTop .grayBlock {
  height: 10px;
  background-color: #DDDDDD;
}
.policyPreview .policyTop .mint-cell .mint-cell-wrapper {
  border: 0;
}
.policyPreview .page-container ul {
  margin: 0 10px;
}
.policyPreview .page-container ul .secureInfo {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
.policyPreview .page-container .bdDetails {
  padding: 20px 10px;
  background: #f7f7f7;
  font-size: 20px;
  font-weight: 400;
  border-left: 1px solid red;
}
.policyPreview .page-container .title {
  border-bottom: 1px solid #ededed;
}
.policyPreview .page-container .arrowImg {
  width: 8px;
  float: right;
}
.policyPreview .page-container .mint-cell .mint-cell-wrapper .mint-cell-title .mint-cell-text {
  color: #b5b5b5;
  font-weight: 300;
}
.policyPreview .page-container .mint-field .mint-cell-wrapper .mint-cell-title {
  width: 110px;
}
.policyPreview .page-container .mint-field .mint-cell-wrapper .mint-cell-value .mint-field-core {
  text-align: right;
}
.policyPreview .page-container .mint-field .mint-cell-wrapper .mint-cell-value .mint-field-other i {
  font-size: 25px;
  color: red;
}
.policyPreview .page-container .title {
  color: black;
  padding: 12px 10px;
}
.policyPreview .page-container .xzInfo {
  text-align: center;
  width: 100%;
  color: #b5b5b5;
  font-weight: 300;
  margin-top: 15px;
  border-collapse: collapse;
  font-size: 14px;
}
.policyPreview .page-container .xzInfo tr th {
    background-color: #f7f7f7;
}
.policyPreview .page-container .xzInfo tr th, .policyPreview .page-container .xzInfo tr td {
    border: 1px solid #ededed !important;
    padding: 5px 0;
    vertical-align: middle;
}
.policyPreview .page-container .xzInfo tr td:nth-of-type(1) {
    width: 40%;
    /*text-align: left;*/
}
.policyPreview .page-container .xzInfo tr td:nth-of-type(2) {
    width: 24%;
}
.policyPreview .page-container .xzInfo tr td:nth-of-type(3) {
    width: 15%;
}
.policyPreview .page-container .xzInfo tr td:nth-of-type(4) {
    width: 21%;
    /*text-align: right;*/
}
.policyPreview .page-footer {
  margin-bottom: 10px;
  margin-bottom: 51px;
}
.policyPreview .page-footer .ydxy {
    margin-top: 10px;
    text-align: center;
}
.policyPreview .page-footer .ydxy a {
      color: #fb0000;
      opacity: 0.6;
      font-size: 12px;
      text-decoration: none;
}
.policyPreview .page-bottom-area {
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 5px 10px;
  background-color: #ece8e8;
}
.policyPreview .page-bottom-area .mint-button--primary {
    background: red;
}
.policyPreview .order .mint-cell-text {
  color: #666 !important;
}



