
.policyDetail {
  font-size: 14px;
}
.policyDetail .policyTop .mint-cell-wrapper {
    font-size: 16px;
}
.policyDetail .policyTop .mint-cell-wrapper {
    background-image: linear-gradient(180deg, #fff, #fff 50%, transparent 50%);
}
.policyDetail .policyTop img {
    width: 110px;
    height: 43px;
    margin-top: 5px;
}
.policyDetail .policyTop span {
    color: #333;
}
.policyDetail .policyTop .grayBlock {
    height: 10px;
    background-color: #F7F7F7;
}
.policyDetail .policyTop .mint-cell .mint-cell-wrapper {
    border: 0;
}
.policyDetail .page-container ul {
    /*margin: 0 10px;*/
}
.policyDetail .page-container ul .secureInfo {
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg);
}
.policyDetail .page-container .bdDetails {
    padding: 20px 10px;
    background: #f7f7f7;
    font-size: 20px;
    font-weight: 400;
    border-left: 1px solid red;
}
.policyDetail .page-container .title {
    border-bottom: 1px solid #E8E8E8;
    color: #9C9A9C;
    padding: 12px 10px;
    background: #F7F7F7;
    -webkit-tap-highlight-color: transparent;
}
.policyDetail .page-container .arrowImg {
    width: 8px;
    float: right;
}
.policyDetail .page-container .mint-cell .mint-cell-wrapper .mint-cell-title .mint-cell-text {
    color: #9C9C9C;
    font-weight: 300;
}
.policyDetail .page-container .mint-cell-value {
    color: #666666;
}
.policyDetail .page-container .mint-field .mint-cell-wrapper .mint-cell-title {
    width: 110px;
}
.policyDetail .page-container .mint-field .mint-cell-wrapper .mint-cell-value .mint-field-core {
    text-align: right;
}
.policyDetail .page-container .mint-field .mint-cell-wrapper .mint-cell-value .mint-field-other i {
    font-size: 25px;
    color: red;
}
.policyDetail .page-container .xzInfo {
    text-align: center;
    width: 100%;
    color: #b5b5b5;
    font-weight: 300;
    margin: 10px 0;
    border-collapse: collapse;
    font-size: 14px;
}
.policyDetail .page-container .xzInfo tr th {
      background-color: #f7f7f7;
}
.policyDetail .page-container .xzInfo tr th, .policyDetail .page-container .xzInfo tr td {
      border: 1px solid #E8E8E8 !important;
      padding: 5px 0;
      vertical-align: middle;
}
.policyDetail .page-container .xzInfo tr td:nth-of-type(1) {
      width: 40%;
      /*text-align: left;*/
}
.policyDetail .page-container .xzInfo tr td:nth-of-type(2) {
      width: 24%;
}
.policyDetail .page-container .xzInfo tr td:nth-of-type(3) {
      width: 15%;
}
.policyDetail .page-container .xzInfo tr td:nth-of-type(4) {
      width: 21%;
      /*text-align: right;*/
}
.policyDetail .page-footer {
    margin-bottom: 10px;
    margin-bottom: 51px;
}
.policyDetail .page-footer .ydxy {
      margin-top: 10px;
      text-align: center;
}
.policyDetail .page-footer .ydxy a {
        color: #fb0000;
        opacity: 0.6;
        font-size: 12px;
        text-decoration: none;
}
.policyDetail .page-bottom-area {
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 5px 10px;
    background-color: #e8e8e8;
}
.policyDetail .page-bottom-area .mint-button--primary {
      background: red;
}
.policyDetail .orders {
    height: 48px !important;
}
.policyDetail .orders .mint-cell-text {
      color: #666 !important;
}
.policyDetail .mint-cell-wrapper {
    font-size: 14px;
}
.policyDetail .topBottom {
    min-height: 0 !important;
}



