
.policyInsure .page-header {
  padding: 20px 0;
  background: #F7F7F7;
}
.policyInsure .page-header span {
    padding: 0 0 0 10px;
    font-size: 16px;
    font-weight: 300;
    color: #666;
}
.policyInsure .page-header a {
    color: red;
    float: right;
    font-size: 14px;
}
.policyInsure .page-container .mint-field-core {
  text-align: right;
}
.policyInsure .page-container .mint-cell-text {
  font-size: 14px;
  white-space: nowrap;
  color: #9C9A9C;
}
.policyInsure .page-container .mint-cell-value {
  font-size: 14px;
  color: #666;
}
.policyInsure .page-container .mint-cell-value a {
    text-decoration: underline;
    color: blue;
}
.policyInsure .page-container .text-left .mint-cell .mint-cell-value {
  font-size: 14px;
  color: #666;
}
.policyInsure .page-container .text-left .mint-cell .mint-cell-wrapper .mint-cell-value input {
  text-align: left;
}
.policyInsure .page-container .text-left .mint-cell .mint-cell-wrapper .mint-cell-value textarea {
  width: 100%;
  text-align: left;
  resize: none;
  overflow: hidden;
}
.policyInsure .page-container .text-left .mint-cell .mint-cell-wrapper .mint-cell-value .mint-field-state {
  margin-left: 0;
}
.policyInsure .page-container .mint-popup {
  width: 100%;
}
.policyInsure .page-container .normal-break {
  margin-left: 15px;
  white-space: normal;
  text-overflow: ellipsis;
  word-wrap: break-word;
  -webkit-line-clamp: 2;
}
.policyInsure .page-container .validity {
  position: relative;
}
.policyInsure .page-container .absolute {
  position: absolute;
  top: 0;
  right: 100px;
  padding-bottom: 8px;
}
.policyInsure .page-container .absolute .mint-radio-core, .policyInsure .page-container .absolute .mint-checkbox-core {
    width: 15px;
    height: 15px;
}
.policyInsure .page-container .absolute .mint-radio-core::after {
    top: 4px;
    left: 4px;
    width: 5px;
    height: 5px;
}
.policyInsure .page-container .absolute .mint-checkbox-core:after {
    top: 2px;
    left: 5px;
    width: 3px;
    height: 7px;
}
.policyInsure .page-container .absolute .mint-radio-label, .policyInsure .page-container .absolute .mint-checkbox-label {
    font-size: 90%;
    margin-right: 10px;
}
.policyInsure .page-bottom-area {
  text-align: center;
  /*position: fixed;*/
  bottom: 0;
  left: 0;
  right: 0;
  padding: 5px 10px;
  background-color: #ece8e8;
}
.policyInsure .page-bottom-area .mint-button--primary {
    background: red;
}



