
.occup-picker-transition-enter-active,
.occup-picker-transition-leave-active {
  transition: opacity 0.3s linear;
}
.occup-picker-transition-enter,
.occup-picker-transition-leave-to {
  opacity: 0;
}
.ocpicker-slide-enter-active,
.ocpicker-slide-leave-active {
  height: 60%;
  transition: all 0.3s linear;
}
.ocpicker-slide-enter,
.ocpicker-slide-leave-to {
  height: 0%;
}



