
.image-uploader .preview-button {
  margin-right: 20px;
  height: 30px;
  border: 1px solid #ccc;
}
.image-uploader .preview-pop {
  width: 100%;
  height: 561px;
  display: table;
  text-align: center;
  background-color: transparent;
}
.image-uploader .preview-pop .img-area {
    display: table-cell;
    vertical-align: middle;
}
.image-uploader .preview-pop .img-area h1 {
      color: white;
      padding: 10px;
      position: absolute;
      top: 0;
      width: 100%;
}
.image-uploader .preview-pop .img-area img {
      width: 100%;
}
.image-uploader .preview-pop .img-area .mint-swipe {
      height: 80%;
}
.image-uploader .preview-pop .img-area .mint-swipe .mint-swipe-indicators .mint-swipe-indicator {
        opacity: 0.6;
}
.image-uploader .red {
  font-size: 25px;
  color: red;
  margin-top: 3px;
}



