
.businessCard .top {
  background: #fd0000;
  width: 100%;
  height: 220px;
  padding-top: 10px;
  box-sizing: border-box;
  line-height: 1.2;
  text-align: center;
}
.businessCard .top .headImg {
    width: 100px;
    margin: 0 auto;
    position: relative;
}
.businessCard .top .headImg img {
      width: 100px;
}
.businessCard .top .headImg i {
      position: absolute;
      right: 0px;
      bottom: 0px;
      color: #f8fa2e;
      font-size: 30px;
}
.businessCard .top .text {
    color: white;
}
.businessCard .top .text .info span {
      padding-right: 5px;
      display: inline-block;
}
.businessCard .top .text .info a {
      text-decoration: none;
      color: white;
}
.businessCard .top .text .info a i {
        font-size: 60px;
        vertical-align: middle;
}
.businessCard .bottom .bottom_top {
  margin: 0 auto;
  width: 100px;
  height: 50px;
  border-left: solid 1px #eb0000;
  border-right: solid 1px #eb0000;
  position: relative;
}
.businessCard .bottom .bottom_top .spot {
    width: 10px;
    height: 10px;
    background-color: #eb0000;
    border-radius: 5px;
    position: absolute;
    bottom: -5px;
    right: -5px;
}
.businessCard .bottom .bottom_top::after {
  content: '';
  background-color: #eb0000;
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  position: absolute;
  top: 45px;
  left: -5px;
}
.businessCard .bottom .bottom_body {
  margin: 0 auto;
  border: solid 1px #eb0000;
  border-radius: 20px;
  width: 200px;
  height: 200px;
  padding: 10px;
  box-sizing: border-box;
}
.businessCard .bottom .bottom_body img {
    width: 180px;
}



