
.info-acquisition .page-header {
  padding: 20px 0;
  background: #F7F7F7;
}
.info-acquisition .page-header span {
    padding: 0 0 0 10px;
    font-size: 16px;
    font-weight: 300;
    color: #666;
}
.info-acquisition .mineImg {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  text-align: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 10px 10px 10px 0;
}
.info-acquisition .mineImg .img-inputer-area {
    margin-top: 15px;
}
.info-acquisition .mineImg .img-inputer {
    box-sizing: border-box;
}
.info-acquisition .mineImg .img-inputer--small {
    width: 140px;
}
.info-acquisition .add-img {
  padding: 25px 5px;
  margin-bottom: 30px;
  text-align: center;
}
.info-acquisition .add-img i {
    color: red;
    font-size: 16px;
    font-weight: bold;
}
.info-acquisition .add-img span:nth-of-type(1) {
    font-size: 16px;
    font-weight: 400;
}
.info-acquisition .add-img span:nth-of-type(2) {
    font-size: 12px;
}
.info-acquisition .page-bottom-area {
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 5px 10px;
  background-color: #ece8e8;
}
.info-acquisition .page-bottom-area .mint-button--primary {
    background: red;
}



