
.mint-data {
  width: 100%;
  pointer-events: auto;
}
.mint-data .picker-slot-wrapper, .mint-data .picker-item {
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}
.mint-data .picker-toolbar {
    border-bottom: solid 1px #eaeaea;
}
.mint-data .mint-data-action {
    display: inline-block;
    width: 50%;
    text-align: center;
    line-height: 40px;
    font-size: 16px;
    color: #26a2ff;
}
.mint-data .mint-data-cancel {
    float: left;
}
.mint-data .mint-data-confirm {
    float: right;
}



