
.policySuccess {
  overflow: hidden;
  height: 100vh;
}
.policySuccess .policySuccessLogo img {
    width: 100%;
    display: block;
}
.policySuccess .successContainer {
    background: #faf2f7;
    height: 100%;
    width: 100%;
    text-align: center;
}
.policySuccess .successContainer .tips {
      color: #8b898c;
      font-size: 15px;
      padding: 0 15px 15px 15px;
}
.policySuccess .successContainer .page-bottom-area {
      text-align: center;
      /*position: fixed;
		    bottom: 0;
		    left: 0;
		    right: 0;*/
      padding: 0 10px;
      /*background-color: #ece8e8;*/
      background: url("../../../assets/1050/bg.png") repeat;
}
.policySuccess .successContainer .page-bottom-area .mint-button--primary {
        background: red;
}



