
.mint-sub .top64 {
  margin-top: 64px;
}
.mint-sub .mint-header {
  color: #000;
  /*margin-top: 10px;*/
  height: 50px;
  line-height: 50px;
}
.mint-sub .mint-header .header-title {
    font-size: 16px;
    line-height: 2;
}
.mint-sub .mint-header i {
    font-size: 20px;
    margin-right: 10px;
}
.mint-sub .stitle {
  background-color: #F7F7F7;
}
.mint-sub .stitle .mint-checklist-title {
    font-size: 16px;
    color: #000;
    padding-top: 7px;
    line-height: 2;
}
.mint-sub .stitle.checklist-title-none .mint-checklist-title {
  display: none;
}
.mint-sub .mint-data-sub {
  width: 100%;
  height: 100%;
  /*margin-top: 45px;*/
  pointer-events: auto;
}
.mint-sub .mint-data-sub .close {
    position: absolute;
    right: 20px;
    top: 20px;
}
.mint-sub .mint-data-sub .close i {
      font-size: 20px;
}
.mint-sub .mint-data-sub .mint-field-core {
    text-align: right;
}
.mint-sub .mint-data-sub .mint-cell-title,
  .mint-sub .mint-data-sub .mint-cell-value {
    font-size: 93%;
}
.mint-sub .overflow-auto {
  overflow: auto;
}
.mint-sub .mint-sub-action {
  width: 100%;
  text-align: right;
  position: fixed;
  top: 0;
  padding: 10px 0 10px 0;
  background-color: white;
  pointer-events: auto;
}
.mint-sub .mint-sub-action span {
    border-radius: 100%;
    vertical-align: middle;
    margin-right: 15px;
    background-color: #ccc;
    padding: 4.5px 10px;
    color: #000;
}
.mint-sub .checklist-right .mint-checklist-label {
  padding-left: 0;
  padding-right: 0;
}
.mint-sub .checklist-right .mint-checkbox-label {
  color: #9C9A9C;
  margin-left: 0;
}



