
.PB-48 {
  padding-bottom: 56px;
}
.app_footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 56px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  box-sizing: border-box;
  background-color: #fff;
  border-top: 1px solid #E8E8E8;
}
.app_footer a {
    display: block;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
    text-decoration: none;
    font-size: 12px;
    color: grey;
}
.app_footer a i {
      font-size: 30px;
}
.app_footer .router-link-exact-active i,
  .app_footer .router-link-active i {
    color: red;
}
.app_footer .router-link-exact-active p,
  .app_footer .router-link-active p {
    color: red;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s ease;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.child-view {
  position: relative;
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1);
}
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
  transform: translate(30px, 0);
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
  transform: translate(-30px, 0);
}



