
.page-swipe .mint-swipe {
  width: 100%;
  height: 141px;
  color: #fff;
  font-size: 30px;
  text-align: center;
}
.page-swipe .swipe-title .mint-swipe-indicators {
  text-align: right;
  right: 0;
  left: auto;
  bottom: 0;
  line-height: 1.2;
}
.page-swipe .slide {
  position: relative;
}
.page-swipe .slide img {
  width: 100%;
  height: 141px;
}
.page-swipe .slide .title {
  width: 100%;
  position: absolute;
  bottom: 0;
  background-color: gray;
  opacity: 0.6;
  font-size: 13px;
  text-align: left;
  line-height: 1.5;
  padding-left: 10px;
  height: 25px;
}



