
.policyBenefic .page-header {
  padding: 20px 0;
  background: #F7F7F7;
}
.policyBenefic .page-header span {
    padding: 0 0 0 10px;
    font-size: 16px;
    font-weight: 300;
    color: #666;
}
.policyBenefic .page-container .mint-field-core {
  text-align: right;
}
.policyBenefic .page-container .mint-cell-text {
  white-space: nowrap;
  color: #9C9A9C;
  font-size: 14px;
}
.policyBenefic .page-container .mint-cell-value {
  color: #666666;
  font-size: 14px;
}
.policyBenefic .page-container .text-left .mint-cell-value input {
  text-align: left;
}
.policyBenefic .page-container .mint-popup {
  width: 100%;
}
.policyBenefic .page-bottom-area {
  text-align: center;
  /*position: fixed;*/
  bottom: 0;
  left: 0;
  right: 0;
  padding: 5px 10px;
  background-color: #ece8e8;
}
.policyBenefic .page-bottom-area .mint-button--primary {
    background: red;
}



