
.benefic-template .validity {
  position: relative;
}
.benefic-template .solt-wrapper {
  position: absolute;
  top: 0;
  right: 100px;
  padding-bottom: 8px;
}
.benefic-template .solt-wrapper .mint-cell-wrapper {
    /*background-image: linear-gradient(180deg, #d9d9d9, #d9d9d9 0%, transparent 50%);*/
    background-image: none;
}
.benefic-template .solt-wrapper .mint-radio-core, .benefic-template .solt-wrapper .mint-checkbox-core {
    width: 15px;
    height: 15px;
}
.benefic-template .solt-wrapper .mint-radio-core::after {
    top: 4px;
    left: 4px;
    width: 5px;
    height: 5px;
}
.benefic-template .solt-wrapper .mint-checkbox-core:after {
    top: 2px;
    left: 5px;
    width: 3px;
    height: 7px;
}
.benefic-template .solt-wrapper .mint-radio-label, .benefic-template .solt-wrapper .mint-checkbox-label {
    font-size: 90%;
    margin-right: 10px;
}
.benefic-template .removedeadBeneficiary {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 20px 10px;
}
.benefic-template .removedeadBeneficiary .left i {
    color: red;
    font-size: 20px;
}
.benefic-template .removedeadBeneficiary .left .gray {
    color: gray;
}
.benefic-template .adddeadBeneficiary, .benefic-template .addaliveBeneficiary {
  padding: 20px 5px;
  text-align: center;
}
.benefic-template .adddeadBeneficiary i, .benefic-template .addaliveBeneficiary i {
    color: red;
    font-size: 20px;
    font-weight: bold;
}
.benefic-template .adddeadBeneficiary span:nth-of-type(1), .benefic-template .addaliveBeneficiary span:nth-of-type(1) {
    font-size: 20px;
    font-weight: 400;
}
.benefic-template .adddeadBeneficiary span:nth-of-type(2), .benefic-template .addaliveBeneficiary span:nth-of-type(2) {
    font-size: 12px;
}
.benefic-template .benefit .mint-field-state {
  margin-left: 0;
}



