
.productPage .productPage_nav .mint-navbar {
  /*border-bottom: 2px solid rgb(221, 223, 222);*/
  box-shadow: 2px 2px 2px #dddfde;
}
.productPage .productPage_nav .mint-navbar .mint-tab-item {
    padding: 15px 0;
}
.productPage .productPage_nav .mint-navbar .mint-tab-item .mint-tab-item-label {
      font-size: 16px;
}
.productPage .productPage_nav .mint-navbar .is-selected {
    border-bottom: 3px solid #fe0000;
    color: #fe0000;
}
.productPage .productPage_container {
  margin-top: 60px;
}
.productPage .productPage_container div div ul li {
    /*padding: 0px 10px 10px 10px;*/
}
.productPage .productPage_container div div ul li img[lazy=loaded] {
      width: 100%;
}
.productPage .productPage_container div div ul li .top img {
      width: 100%;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
}
.productPage .productPage_container div div ul li .bottom {
      padding: 10px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
.productPage .productPage_container div div ul li .bottom .title {
        color: #393839;
}
.productPage .productPage_container div div ul li .bottom .price span {
        color: #fc0000;
}
.productPage .productPage_container div div ul li .dec {
      padding: 0px 10px 10px 10px;
      color: #666;
      font-size: 13px;
      line-height: 1.5;
}



