}
.certification2 .inviterInfo .inviterLogo {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      padding: 10px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.certification2 .inviterInfo .inviterLogo img {
        width: 100%;
}
.certification2 .inviterInfo .inviterText {
      -webkit-box-flex: 2.5;
