}
.certification2 .mineInfo {
    padding-top: 60px;
}
.certification2 .mineInfo .mint-field .mint-cell-wrapper .mint-cell-value .mint-field-core {
      text-align: right;
}
.certification2 .mint-cell .mint-cell-wrapper .mint-cell-text {
    color: #9C9C9C;
}
.certification2 .mint-cell .mint-cell-wrapper .mint-cell-value {
