}
.certification2 .guarantype {
    padding: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.certification2 .guarantype .left {
      color: #9C9C9C;
}
.certification2 .guarantype .right {
      color: #666;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.certification2 .guarantype .right .guarantype_label {
        width: 15px;
        position: relative;
}
.certification2 .guarantype .right .guarantype_label .checkbox {
          position: absolute;
          width: 15px;
          height: 15px;
          top: 0;
          left: 0;
          border: 1px solid #ddd;
          background: #fb0000;
}
.certification2 .guarantype .right .guarantype_label .checkbox:after {
          content: '';
          position: absolute;
          width: 8px;
          height: 4px;
          background: transparent;
          top: 3px;
          left: 3px;
          border: 2px solid white;
          border-top: none;
          border-right: none;
          -webkit-transform: rotate(-45deg);
                  transform: rotate(-45deg);
}
.certification2 .guarantype .right .guarantype_label .checkboxActive {
          position: absolute;
          width: 15px;
          height: 15px;
          top: 0;
          left: 0;
          border: 1px solid rgba(251, 0, 0, 0.6);
}
.certification2 .guarantype .right .guarantype_label #guarantype {
          visibility: hidden;
}
.certification2 .dbrInfo .mint-field .mint-cell-wrapper .mint-cell-value .mint-field-core {
    text-align: right;
}
.certification2 .mint-button--primary {
    background: #fb0000;
}



