
.prospectus {
  background: #eae9e9;
}
.prospectus img {
    width: 100%;
}
.prospectus .prospectus_logo img {
    width: 100%;
    display: block;
}
.prospectus .prospectus_container {
    padding: 0 10px;
}
.prospectus .prospectus_container .prospectus_securityPlan {
      background: white;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
}
.prospectus .prospectus_container .prospectus_securityPlan .securityPlan_title {
        text-align: center;
        font-size: 16px;
        line-height: 3;
        border-bottom: 1px solid #E8E8E8;
        color: #666;
}
.prospectus .prospectus_container .prospectus_securityPlan .securityPlan_container {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        text-align: center;
        line-height: 2;
        color: #9C9C9C;
        font-size: 14px;
}
.prospectus .prospectus_container .prospectus_securityPlan .securityPlan_container span {
          color: #666;
}
.prospectus .prospectus_container .prospectus_securityPlan .securityPlan_container .left .left_top {
          border-bottom: 1px solid #E8E8E8;
}
.prospectus .prospectus_container .prospectus_securityPlan .securityPlan_container .right .right_top {
          border-bottom: 1px solid #E8E8E8;
}
.prospectus .prospectus_container .prospectus_table {
      background: white;
      font-size: 12px;
      text-align: center;
      color: #9C9C9C;
}
.prospectus .prospectus_container .prospectus_table tr td {
        padding: 10px 3px;
        min-width: 50px;
        vertical-align: middle;
        border: 1px solid #E8E8E8;
}
.prospectus .prospectus_container .prospectus_hr {
      width: 100%;
      height: 10px;
      background: #eae9e9;
}
.prospectus .prospectus_container .existenceInsurance {
      background: white;
}
.prospectus .prospectus_container .existenceInsurance .existenceInsurance_title {
        text-align: center;
        font-size: 16px;
        line-height: 3;
        border-bottom: 1px solid #E8E8E8;
        color: #666;
}
.prospectus .prospectus_container .existenceInsurance .existenceInsurance_title_container {
        font-size: 14px;
        padding: 10px;
        padding-bottom: 30px;
        line-height: 2;
        color: #9C9C9C;
}
.prospectus .prospectus_container .existenceInsurance .existenceInsurance_title_container li .num {
          color: #666;
          font-size: 16px;
}
.prospectus .prospectus_container .existenceInsurance .existenceInsurance_title_container li .price {
          color: #666;
          font-size: 16px;
}
.prospectus .prospectus_container .existenceInsurance .existenceInsurance_title_container p {
          font-size: 12px;
}
.prospectus .prospectus_container .existenceInsurance_container {
      margin-top: 15px;
      background: white;
      padding-top: 30px;
      margin-bottom: 15px;
      padding: 10px;
      line-height: 2;
      color: #9C9C9C;
      font-size: 14px;
}
.prospectus .prospectus_container .existenceInsurance_container .existenceInsurance_title {
        text-align: center;
        font-size: 16px;
        line-height: 3;
        border-bottom: 1px solid #E8E8E8;
        color: #313031;
}
.prospectus .prospectus_container .existenceInsurance_container li .num {
        color: #666;
        font-size: 16px;
}
.prospectus .prospectus_container .existenceInsurance_container li .price {
        color: #666;
        font-size: 16px;
}
.prospectus .prospectus_container .existenceInsurance_container p {
        font-size: 12px;
}
.prospectus .prospectus_container .clauseDetails {
      display: block;
      text-decoration: none;
      margin: 15px 0;
      padding: 15px;
      background: white;
      text-align: center;
      font-size: 16px;
      color: #666;
      position: relative;
      border-radius: 5px;
}
.prospectus .prospectus_container .clauseDetails .fr {
        position: absolute;
        color: #666;
        font-size: 30px;
        right: 0;
        top: 10px;
}
.prospectus .prospectus_container .tip {
      color: grey;
      padding: 10px;
      font-size: 12px;
      line-height: 2;
}



