.minepage .mine-infor {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 200px;
    /*background: rgb(253, 0, 0);*/
    background: url(../../assets/mine-bg.png) top center no-repeat;
    padding-left: 20px;
}
.minepage .mine-infor .container .flex {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.minepage .mine-infor .container .flex .img {
        width: 80px;
}
.minepage .mine-infor .container .flex .img img {
          width: 100%;
}
.minepage .mine-infor .container .flex .text {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        margin-left: 20px;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        margin-top: 20px;
}
.minepage .mine-infor .container .flex .text .title {
          color: white;
          font-size: 20px;
          letter-spacing: 2px;
}
.minepage .mine-infor .container .flex .text .identifi {
          color: #f4e927;
          margin-top: 6px;
}
.minepage .mine-infor .container .flex .text .identifi i {
            font-size: 14px;
}
.minepage .mine-infor .container .editMineinfo {
      position: absolute;
      right: 5px;
      bottom: 10px;
}
.minepage .mine-infor .container .editMineinfo a {
        text-decoration: none;
        font-size: 14px;
        color: #fff;
}
.minepage .mine-infor .container .editMineinfo a i {
          font-size: 14px;
}
.minepage .mine-list .mint-cell {
    background: none;
}
.minepage .mine-list .mint-cell .mint-cell-wrapper {
      /*border-bottom: 1px solid #e1e0e0;*/
      font-size: 15px;
      line-height: 2.8;
      color: #242524;
}
.minepage .mine-list .mint-cell .mint-cell-wrapper .iconfont {
        color: #f90000;
        font-size: 20px;
        padding-right: 10px;
        vertical-align: middle;
}
.minepage .mine-list .mint-cell .mint-cell-allow-right:after {
      width: 8px;
      height: 8px;
      border-width: 1px;
      border-bottom-width: 0;
      border-left-width: 0;
}



