
@import url("//at.alicdn.com/t/font_352963_rjs1kshfs41mj9k9.css");
.friends {
}
.friends a, .friends a:hover, .friends a:active, .friends a:visited, .friends a:link, .friends a:focus {
    -webkit-tap-highlight-color: transparent;
    -webkit-tap-highlight-color: transparent;
    outline: none;
    background: none;
    text-decoration: none;
}
.friends .topImg {
    width: 100%;
    display: block;
}
.friends .icon-haoyou {
    font-size: 18px;
}
.friends .curInvite {
    border-left: 1px solid #FFFFFF;
    margin: 5px 0;
}
.friends .mint-tab-item-label {
    font-size: 16px;
}
.friends .mint-tab-item {
    border-bottom: 3px solid #DDDDDD;
    margin-bottom: -3px;
}
.friends .inviteRule {
    border-right: 1px solid #DDDDDD !important;
}
.friends .inviteTab .is-selected {
    border-bottom: 2px solid #fe0000 !important;
    color: #fe0000 !important;
    text-align: left;
    padding-left: 30px;
}
.friends ul li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.friends ul li:nth-of-type(1) .rankingImg {
    display: inline-block;
    width: 20px;
    height: 30px;
    background: url(../../../assets/mine/gold.png);
    background-size: 20px 30px;
}
.friends ul li:nth-of-type(2) .rankingImg {
    display: inline-block;
    width: 20px;
    height: 30px;
    background-image: url(../../../assets/mine/silver.png);
    background-size: 20px 30px;
}
.friends ul li:nth-of-type(3) .rankingImg {
    display: inline-block;
    width: 20px;
    height: 30px;
    background-image: url(../../../assets/mine/cuprum.png);
    background-size: 20px 30px;
}
.friends .inviteList {
    margin: 0 10px;
    border-bottom: 1px solid #DDDDDD;
    padding: 10px 0;
    position: relative;
}
.friends .inviteList img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
}
.friends .inviteMsg {
    padding-left: 15px;
    line-height: 40px;
    font-size: 14px;
}
.friends .grayTex {
    color: #a4a4a4;
}
.friends .inviteBg {
    background-color: #DDDDDD;
    padding-bottom: 10px;
}
.friends .redBg {
    background-color: #e70014;
}
.friends .redBg li {
      display: inline-block;
      width: 49%;
      text-align: center;
      color: #FFFFFF;
      line-height: 50px;
      font-size: 18px;
}
.friends .redBg li i {
        color: #fff000;
}
.friends .redBg li a {
        color: #FFFFFF;
}
.friends .qcode {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 100;
    text-align: center;
    display: none;
}
.friends .qcode img {
      width: 60%;
      margin: 200px auto;
}
.friends .inviteMsg img {
    width: 20px;
    height: 30px;
}
.friends .inviteRuleList {
    padding: 15px 30px;
    line-height: 30px;
    color: #999999;
    font-size: 14px;
}
.friends .popShare {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.6);
    width: 100%;
    height: 100vh;
    text-align: center;
    padding-top: 200px;
}
.friends .lastNote {
    text-align: center;
    font-size: 14px;
    color: #666666;
    line-height: 30px;
}



