
.order {
  background-color: #f4f3f3;
  min-height: 100vh;
}
.order a, .order a:hover, .order a:active, .order a:visited, .order a:link, .order a:focus {
    -webkit-tap-highlight-color: transparent;
    -webkit-tap-highlight-color: transparent;
    outline: none;
    background: none;
    text-decoration: none;
}
.order .navbar {
    position: relative;
}
.order .badge {
    display: none;
    position: absolute;
    top: 5px;
    right: 0px;
    border: 1px solid #F26464;
    color: #F26464;
    border-radius: 50%;
    padding: 0 3px;
    font-size: 10px;
}
.order .mint-tab-item:nth-of-type(2) .badge {
    display: block;
}
.order .mint-tab-item-label {
    font-size: 16px;
}
.order .mint-tab-container-item {
    padding-bottom: 20px;
}
.order .mint-navbar .mint-tab-item.is-selected {
    border-color: red;
    color: red;
}
.order .mint-cell {
    min-height: 0;
}
.order .orderName {
    color: #333;
    width: 80%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.order .orderList {
    position: relative;
    padding: 10px;
    margin: 15px 15px 0 15px;
    line-height: 30px;
    border: 1px solid #D4D3D4;
    border-radius: 10px;
    background-color: #FFFFFF;
    -webkit-tap-highlight-color: transparent;
}
.order .logo {
    position: absolute;
    top: 10px;
    right: 5px;
}
.order .logo img {
      width: 60px;
}
.order .width120 {
    display: inline-block;
    width: 80px;
}
.order .orderStatus {
    color: red;
    float: right;
}
.order .grayBlock1 {
    height: 52px;
    background-color: #DDDDDD;
}
.order .grayTex {
    font-size: 14px;
    line-height: 20px;
    color: #A4A4A4;
}
.order .promptMsg {
    color: #f84e5d;
    font-size: 12px;
    background-color: #DDDDDD;
    line-height: 30px;
    text-align: center;
}
.order .nullOrder {
    text-align: center;
    height: 100vh;
    background-color: #DDDDDD;
}
.order .nullOrder .nullImg {
      padding: 150px 0 80px;
}
.order .nullOrder .nullImg img {
        width: 40%;
}
.order .nullOrder .nullImg p {
        color: #4e4e4e;
        font-size: 20px;
        padding-top: 20px;
}
.order .nullOrder .nullText {
      color: #8e8e8e;
      padding-bottom: 10px;
}
.order .nullOrder a {
      color: red;
}



