
.intelist {
  min-height: 100%;
  color: #666;
  font-size: 14px;
}
.intelist .intelist-tab {
    position: fixed;
    top: 0px;
    left: 0;
    z-index: 5;
    width: 100%;
}
.intelist .intelist-tab .mint-navbar .mint-tab-item {
      font-size: 14px;
      border-bottom: 1px solid #eee;
      border-left: none;
}
.intelist .intelist-tab .mt-tab-item-font {
      font-size: 16px;
      color: #333;
}
.intelist .intelist-tab .mint-navbar .mint-tab-item.is-selected {
      color: #e70014;
      margin-bottom: 0;
      border-bottom: 2px solid #e70014;
}
.intelist .intelist-con {
    padding-top: 55px;
}
.intelist .intelist-con p {
      padding: 15px;
      color: #e60012;
}
.intelist .intelist-con-fl {
    position: fixed;
    top: 50px;
    left: 0;
    min-height: 100%;
    width: 60px;
    background-color: #e9e9e9;
    text-align: center;
    bottom: 0;
    overflow-y: scroll;
}
.intelist .intelist-con-fl li {
      height: 30px;
      line-height: 30px;
      border-top: 1px solid #eee;
}
.intelist .intelist-con-fl .mint-navbar {
      display: block;
      background: none;
}
.intelist .intelist-con-fl .mint-navbar .mint-tab-item {
      padding: 0;
      border-bottom: 1px solid #eee;
}
.intelist .intelist-con-fl .mint-navbar .mint-tab-item:first-of-type {
        color: #e70014;
        background: #fff;
}
.intelist .intelist-con-fl .mint-navbar .mint-tab-item .mint-tab-item-label {
        display: block;
        height: 50px;
        line-height: 50px;
}
.intelist .intelist-con-fl .mint-navbar .mint-tab-item.is-selected {
      margin-bottom: 0;
      border-left: 2px solid #e70014;
      color: #9c9a9c;
      background-color: #fff;
}
.intelist .intelist-con-fl .mint-navbar .mint-tab-item.is-selected:first-of-type {
        color: #e70014;
}
.intelist .intelist-con-fl .mint-navbar span {
      display: block;
      background: #fff url(../../../assets/mengzhu.png) center center no-repeat;
      background-size: 100%;
      height: 30px;
      line-height: 30px;
      color: #fdde23;
      text-shadow: 3px 0 1px rgba(0, 0, 0, 0.1);
      font-size: 12px;
}
.intelist .intelist-con-fr {
    margin-left: 70px;
    padding-right: 10px;
}
.intelist .intelist-con-fr .infor {
      border-bottom: 1px solid #ddd;
      padding: 10px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.intelist .intelist-con-fr .img-box {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      overflow: hidden;
}
.intelist .intelist-con-fr .img-box img {
        width: 80px;
        max-width: 100%;
        margin-right: 10px;
}
.intelist .intelist-con-fr .levelone li {
      position: relative;
      background-color: #fff;
      padding: 15px 10px;
      line-height: 24px;
      border-bottom: 1px solid #ddd;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
}
.intelist .intelist-con-fr .levelone li:last-of-type {
        border-bottom: none;
}
.intelist .intelist-con-fr .leveltwo .leveltwotitle {
      position: relative;
      background: #fafafa;
      padding: 20px 10px;
      border-left: 2px solid #eee;
      border-bottom: 1px solid #eee;
      color: #666;
}
.intelist .intelist-con-fr .leveltwo .active {
      border-left: 2px solid #f00;
}
.intelist .intelist-con-fr .leveltwo .leveltwocon {
      line-height: 30px;
      padding: 15px;
      font-size: 12px;
      color: #666;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      border-bottom: 1px solid #eee;
}
.intelist .intelist-con-fr .leveltwo .leveltwocon:last-of-type {
        border-bottom: none;
}
.intelist .intelist-con-fr .item {
      color: #313031;
      display: block;
}
.intelist .intelist-con-fr .item:nth-of-type(2) {
        color: #9c9a9c;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
}
.intelist .intelist-con-fr .item:nth-of-type(2) em {
          color: #e60013;
}
.intelist .intelist-con-fr .ach {
      font-size: 12px;
      display: block;
      position: absolute;
      top: 0px;
      right: 0px;
      display: inline-block;
      padding: 5px;
      color: #fdde23;
}
.intelist .intelist-con-fr .ach img {
        width: 50px;
        opacity: .3;
}



