
.field .wx-field {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.field .wx-field textarea {
    -webkit-box-align: inherit;
        -ms-flex-align: inherit;
            align-items: inherit;
}
.field .wx-field textarea .mint-cell-title {
      padding: 10px 0;
}
.field .wx-field textarea .mint-cell-value {
      padding: 5px 0;
}
.field .wx-field .mint-cell-title {
    width: 105px;
    -webkit-box-flex: 0;
        -ms-flex: none;
            flex: none;
}
.field .wx-field .mint-cell-value {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    color: inherit;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.field .wx-field .mint-field-core {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border-radius: 0;
    border: 0;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    outline: 0;
    line-height: 1.6;
    font-size: inherit;
    width: 100%;
}
.field .wx-field .mint-field-clear {
    opacity: .2;
}
.field .wx-field .mint-field-state {
    color: inherit;
    margin-left: 20px;
}
.field .wx-field .mint-field-state .mintui {
      font-size: 20px;
}
.field .wx-field .mint-field-state .mintui-field-error {
      color: #f44336;
}
.field .wx-field .mint-field-state .mintui-field-warning {
      color: #ffc107;
}
.field .wx-field .mint-field-state .mintui-field-success {
      color: #4caf50;
}
.field .wx-field .mint-field-state .mintui-field-default {
      margin-left: 0;
}
.field .wx-field .mintui-field-other {
    position: relative;
}
.field .wx-field.is-notitle .mint-cell-title {
  display: none;
}
.field .wx-field.error-label .mint-cell-label {
  color: red;
}
.field .lineerror {
  background: #ff697f;
  text-align: center;
  padding: 8px;
  color: white;
  position: relative;
  font-weight: 100;
  font-size: 14px;
}
.field .lineerror:before {
  position: absolute;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  border-bottom: 9px solid #ff697f;
  content: " ";
  display: block;
  width: 0;
  height: 0;
  top: -8px;
  right: 30px;
}



