
.inviteFriend {
  background-image: url(../../../assets/mine/friendbackground.png);
  /*background-size: 100% 10px;*/
  height: 100vh;
  overflow: auto;
}
.inviteFriend .friendBg {
    height: 500px;
    background-image: url(../../../assets/mine/friend2.png);
    background-size: 100% 500px;
    position: relative;
}
.inviteFriend .friendMsg {
    font-size: 12px;
    position: absolute;
    top: 268px;
    left: 0;
    width: 100%;
    text-align: center;
    color: #FFFFFF;
}
.inviteFriend .mint-cell {
    background-color: transparent;
}
.inviteFriend input {
    border: 0;
    font-size: 16px;
    width: 260px;
    color: #f7bc87;
    background-color: transparent;
}
.inviteFriend input:-ms-input-placeholder {
    color: #f7bc87;
    opacity: 1;
}
.inviteFriend input::-webkit-input-placeholder {
    color: #f7bc87;
    opacity: 1;
}
.inviteFriend .inviteMsg {
    position: absolute;
    top: 340px;
    left: 0;
    width: 100%;
}
.inviteFriend form {
    width: 260px;
    margin: 0 auto;
    /*border-color: #f7bc87;*/
}
.inviteFriend .mint-button--large {
    background-color: #ff7a00;
}
.inviteFriend p {
    text-align: center;
    font-size: 14px;
    color: #ff7a00;
    margin-bottom: 20px;
}
.inviteFriend .mint-cell-wrapper, .inviteFriend .mint-cell:last-child {
    background-image: linear-gradient(180deg, #f7bc87, #f7bc87 50%, transparent 50%);
}
.inviteFriend .verfifyCode input {
    width: 100px;
}
.inviteFriend .verfifyCode .mint-cell-wrapper {
    padding-right: 0;
}



