
.policyPayment {
  font-size: 14px;
}
.policyPayment .page-container .title {
    color: #666;
    padding: 12px 10px;
    font-size: 16px;
    background: #F7F7F7;
}
.policyPayment .page-container .mint-field .mint-cell-wrapper .mint-cell-title {
    width: 110px;
}
.policyPayment .page-container .mint-field .mint-cell-wrapper .mint-cell-value .mint-field-core {
    text-align: right;
}
.policyPayment .page-container .mint-cell .mint-cell-wrapper .mint-cell-title {
    width: 110px;
}
.policyPayment .page-container .mint-cell .mint-cell-wrapper .mint-cell-value .mint-field-core {
    text-align: right;
}
.policyPayment .page-container .mint-cell-title {
    width: 110px;
}
.policyPayment .page-container .mint-cell-title .mint-cell-text {
      white-space: nowrap;
      color: #9C9A9C;
      font-size: 14px;
}
.policyPayment .page-container .mint-cell-value {
    color: #666;
    font-size: 14px;
}
.policyPayment .page-check {
    height: 50px;
    line-height: 50px;
    /*background-color: #ece8e8;*/
    padding-bottom: 51px;
}
.policyPayment .page-bottom-area {
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 5px 10px;
    background-color: #ece8e8;
}
.policyPayment .page-bottom-area .mint-button--primary {
      background: red;
}



