
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-arrow-r:before {
  content: "\e66a";
}
.integral .item {
  padding: 15px;
}
.integral .item-img {
  display: block;
}
.integral .item-img em {
    font-size: 20px;
}
.integral .img {
  border-radius: 100%;
  width: 100px;
  height: 100px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  overflow: hidden;
  border: 2px solid #fff;
  box-shadow: 0 3px 3px 0.1px rgba(114, 114, 114, 0.75);
  margin-bottom: 10px;
}
.integral .img img {
    max-width: 100%;
}
.integral .row {
  border-bottom: 1px solid #eadcdb;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  line-height: 1.5;
  text-align: center;
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: #999090;
}
.integral .row:first-of-type {
    padding-top: 50px;
    padding-bottom: 50px;
    color: #fff;
}
.integral .row:first-of-type em {
      color: #535353;
}
.integral .row:first-of-type strong {
      color: #e60013;
}
.integral .row:nth-child(2) {
  background: #f00;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  color: #fff;
}
.integral .row:nth-child(2) .item {
    width: 50%;
}
.integral .link {
  font-size: 14px;
  color: #e60013;
  text-decoration: none;
  display: block;
  text-align: center;
  width: 50%;
  margin: 30px auto;
}
.integral .link:hover {
    color: #f00;
}
.integral strong,
.integral em {
  display: block;
}
.integral strong {
  font-size: 16px;
}
.integral strong:first-of-type {
    font-size: 20px;
}
.integral em {
  font-size: 13px;
}



