
.friends .topImg {
  width: 100%;
  display: block;
}
.friends .qcode {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 100;
  text-align: center;
  display: none;
}
.friends .qcode img {
    width: 60%;
    margin: 200px auto;
}
.friends .popShare {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100vh;
  text-align: center;
  padding-top: 200px;
}
.friends .shareInvite {
  text-align: center;
  font-size: 18px;
  background: #e70014;
  height: 60px;
  line-height: 60px;
  color: #FFFFFF;
}
.friends .qcodeInvite {
  text-align: center;
  margin-top: 40px;
}
.friends .qcodeInvite img {
    width: 50%;
}



