
.alliance {
  font-size: 14px;
  color: #666;
  line-height: 18px;
}
.alliance .clearfix:after {
  display: block;
  clear: both;
  content: "";
  visibility: hidden;
  height: 0;
}
.alliance .clearfix {
  zoom: 1;
}
.alliance .alliance-detail-list {
  padding: 15px;
}
.alliance .alliance-detail-list li {
  border: 1px solid #dedfde;
  border-radius: 10px;
  padding: 10px;
  position: relative;
  margin-bottom: 10px;
}
.alliance .alliance-detail-list li .att {
  position: absolute;
  right: 30px;
  top: 15px;
}
.alliance .alliance-detail-list li .att img {
  width: 50px;
}
.alliance .alliance-detail-list dl dt {
  font-size: 15px;
  color: #333;
  margin-bottom: 5px;
}
.alliance .alliance-detail-list dl dt span {
  display: inline-block;
  width: calc(100% - 80px);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.alliance .alliance-detail-list dl dt img {
  float: right;
  width: 80px;
}
.alliance .alliance-detail-list dl dd {
  font-size: 14px;
  color: #666;
  line-height: 20px;
}
.alliance .alliance-detail-list dl dd span {
  display: inline-block;
  width: 70px;
  color: #9c9a9c;
}
.alliance .alliance-detail-list dl dd i {
  float: right;
  color: #f00;
}


