
.identification .inviterInfo {
  width: 98%;
  background: rgba(255, 255, 255, 0.5);
  /*position: fixed;*/
  top: 0;
  z-index: 1;
  height: 60px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0px 4px;
  border-radius: 5px;
  border-bottom: 1px solid #aaa;
  /*box-shadow: 0 1px 1px 1px #aaa;*/
}
.identification .inviterInfo .inviterLogo {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.identification .inviterInfo .inviterLogo img {
      width: 100%;
}
.identification .inviterInfo .inviterText {
    -webkit-box-flex: 2.5;
        -ms-flex: 2.5;
            flex: 2.5;
    font-size: 14px;
    line-height: 60px;
}
.identification .mineInfo {
  /*margin-top: 60px;*/
}
.identification .mineInfo .mint-field .mint-cell-wrapper .mint-cell-value .mint-field-core {
    text-align: right;
}
.identification .mint-cell .mint-cell-wrapper .mint-cell-text {
  color: #9C9C9C;
}
.identification .mint-cell .mint-cell-wrapper .mint-cell-value {
  color: #666;
}
.identification .mint-cell .mint-cell-wrapper .mint-cell-value .mint-field-core {
    text-align: right;
}
.identification .mint-cell .mint-cell-wrapper .mint-cell-value input:disabled {
    background: white;
}
.identification .container {
  margin-bottom: 10px;
}
.identification .container .ydxy {
    color: #666;
    margin-top: 10px;
    text-align: center;
}
.identification .container .ydxy a {
      color: #fb0000;
      opacity: 0.6;
      font-size: 12px;
}
.identification .mint-button--primary {
  background: #fb0000;
}



